import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import "@servicenow/now-button";

import './sn-component-carousel-slide';

import actions from './actions';
import styles from './carousel.scss';
import view from './view';

import { AUTOSCROLL_START, CLEAN_TIMERS, PROPERTIES_SET, directions } from './constants';

createCustomElement('sn-component-carousel', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * Array of slide objects to render into carousel
		 *
		 * @type {Object[]}
		*/
		slides: {
			default: []
		},

		/**
		 * aria-label of the navigation buttons
		 *
		 * @type {Object}
		*/
		navButtonLabels: {
			default: {
				next: 'Next',
				previous: 'Previous',
			}
		},

		/**
		 * Whether the carousel should auto-scroll
		 * @type {boolean}
		*/
		autoscroll: { default: false },

		/**
		 * The speed of auto-scroll, in ms
		 * @type {number}
		*/
		autoscrollSpeed: { default: 7000 },

		/**
		 * Whether the carousel should pause auto-scroll
		 * when hovered
		 * @type {boolean}
		*/
		pauseOnHovered: { default: true },

		/**
		 * The speed of the slide transition animation, in ms
		 * @type {number}
		*/
		slideSpeed: { default: 600 },

		/**
		 * Whether the carousel should navigate in RTL mode
		 * @type {boolean}
		*/
		rtl: { default: false },

		/**
		 * Carousel height, in px
		 * @type {number}
		*/
		height: { default: 416 },

		/**
		 * Handle clicking a slide button
		 * @type {function}
		*/
		handleButtonClick: {},


		/**
		 * Local Properties
		*/

		/**
		 * Whether the carousel is sliding. Used to prevent triggering
		 * a slide before the current slide completes
		 * @type {boolean}
		*/
		isSliding: { default: false },

		/**
		 * Whether the carousel is sliding. Used to prevent triggering
		 * a slide before the current slide completes
		 * @type {boolean}
		*/
		slideDirection: { default: null },

		/**
		 * Holds the timeout id for sliding
		 * @type {number}
		*/
		slideTimeout: { default: null },

		/**
		 * Holds the interval id for auto-scroll
		 * @type {number}
		*/
		autoscrollInterval: { default: null },

		/**
		 * Index of the current active slide
		 * @type {number}
		*/
		active: { default: 0 },

		/**
		 * Index of the last active slide
		 * Used to set visibility during transitions
		 * @type {number}
		*/
		lastActive: { default: 0 },

		/**
		 * Whether to display rounded corners or not
		 * @type {boolean}
		 */
		roundedCorners: { default: false }
	},

	styles,

	actionHandlers: actions
});
