import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import styles from './style.scss';
import view from './view';

createCustomElement('sn-component-carousel-slide', {
	renderer: {
		type: snabbdom,
		view
	},

	properties: {
		/**
		 * Slide heading
		 * @type {string}
		*/
		heading: { default: "" },

		/**
		 * Slide body text
		 * @type {string}
		*/
		bodyText: { default: "" },

		/**
		 * Button text
		 * @type {string}
		*/
		buttonText: { default: "" },

		/**
		 * Button link
		 * @type {string}
		*/
		buttonLink: { default: "" },

		/**
		 * Banner background image
		 * @type {string}
		*/
		backgroundImage: { default: "" },

		/**
		 * Sets the background of the slides to transparent
		 * @type {boolean}
		*/
		backgroundTransparent: { default: false },

		/**
		 * Visible
		 * @type {boolean}
		*/
		visible: { default: false },

		/**
		 * Slide speed
		 * @type {number}
		*/
		slideSpeed: {},

		/**
		 * Transition
		 * @type {string}
		*/
		transition: { default: null },

		/**
		 * Handle clicking a slide button
		 * @type {function}
		*/
		handleButtonClick: { default: link => window.open(link) },

		/**
		 * Enables rich text
		 * @type {boolean}
		*/
		useCustomHtml: { default: false},

		/**
		 * HTML string
		 * @type {string}
		*/
		richText: { default: '' },
		
		/**
		 * id for aria labels
		 * @type {string}
		*/
		carouselId: { default: '' },
		
		/**
		 * Slide index
		 * @type {number}
		*/
		index: {},

	},

	styles
});

