import { actionTypes } from '@servicenow/ui-core';

import {
	AUTOSCROLL_START,
	AUTOSCROLL_STOP,
	NEXT_SLIDE,
	PREVIOUS_SLIDE,
	SET_SLIDE,
	END_SLIDE,
	PROPERTIES_SET,

	directions
} from './constants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED } = actionTypes;

const setNextSlide = ({ state, dispatch }) => {
	const { active, slides, slideSpeed, isSliding } = state.properties;

	if (isSliding)
		return;

	const slideCount = slides.length;

	const nextSlide = active + 1 === slideCount
		? 0
		: active + 1;

	const slideTimeout = setTimeout(() => {
		dispatch(END_SLIDE);
	}, slideSpeed);

	dispatch(PROPERTIES_SET, {
		active: nextSlide,
		lastActive: active,
		isSliding:true,
		slideDirection: directions.RIGHT,
		slideTimeout
	});
};


const setPreviousSlide = ({ state, dispatch }) => {
	const { active, slides, slideSpeed, isSliding } = state.properties;

	if (isSliding)
		return;

	const slideCount = slides.length;

	const nextSlide = active === 0
		? slideCount - 1
		: active - 1;

	const slideTimeout = setTimeout(() => {
		dispatch(END_SLIDE);
	}, slideSpeed);

	dispatch(PROPERTIES_SET, {
		active: nextSlide,
		lastActive: active,
		isSliding: true,
		slideDirection: directions.LEFT,
		slideTimeout
	});
};


const setSlide = ({ state, dispatch, action }) => {
	const { slide } = action.payload;
	const { isSliding, slideSpeed, active } = state.properties;

	if (isSliding)
		return;

	const slideTimeout = setTimeout(() => {
		dispatch(END_SLIDE);
	}, slideSpeed);

	const slideDirection = slide > active ? directions.RIGHT : directions.LEFT;

	dispatch(PROPERTIES_SET, {
		isSliding: true,
		active: slide,
		lastActive: active,
		slideDirection,
		slideTimeout
	});
};


const endSlide = ({ state, dispatch }) => {
	const { slideTimeout } = state.properties;
	clearTimeout(slideTimeout);

	dispatch(PROPERTIES_SET, {
		isSliding: false,
		slideDirection: null
	});
};


const autoscrollStart = ({ state, dispatch }) => {
	const { autoscrollSpeed, autoscrollInterval, rtl } = state.properties;

	if (autoscrollInterval)
		return;

	const interval = setInterval(() => {
		const autoscrollAction = rtl ? PREVIOUS_SLIDE : NEXT_SLIDE
		dispatch(autoscrollAction);
	}, autoscrollSpeed);

	dispatch(PROPERTIES_SET, {
		autoscrollInterval: interval
	});
};


const autoscrollStop = ({ state, dispatch }) => {
	const { autoscrollInterval } = state.properties;

	clearInterval(autoscrollInterval);

	dispatch(PROPERTIES_SET, {
		autoscrollInterval: null
	});
};


const handleComponentBootstrapped = ({ dispatch, properties }) => {
	const { autoscroll, rtl, slides } = properties;

	if (autoscroll)
		dispatch(AUTOSCROLL_START);

	if (rtl) {
		slides.reverse();

		dispatch(PROPERTIES_SET, {
			...properties,
			active: slides.length - 1,
			slides
		});
	}
};

/**
 * Cleanup any intervals or timeouts that this component has
 * set for internal use here
*/
const handleComponentDisconnected = ({ state }) => {
	const { autoscrollInterval, slideTimeout } = state.properties;

	clearInterval(autoscrollInterval);
	clearTimeout(slideTimeout);
};


export default {
	[NEXT_SLIDE]: setNextSlide,
	[PREVIOUS_SLIDE]: setPreviousSlide,
	[SET_SLIDE]: setSlide,
	[END_SLIDE]: endSlide,

	[AUTOSCROLL_START]: autoscrollStart,
	[AUTOSCROLL_STOP]: autoscrollStop,

	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped,
	[COMPONENT_DISCONNECTED]: handleComponentDisconnected
};
