import { createRef } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-rich-text';
import { kebabCase } from 'lodash';

const NOW_BUTTON_HEIGHT = 32;

/**
 * Parse a string for an int value, returning 0 if the result is NaN
 *
 * @param {string} val
 *
 * @returns {number}
 */
const safeParseInt = val => {
	const out = parseInt(val);
	return isNaN(out) ? 0 : out;
};


/**
 * Get the full height of an element, including margins
 *
 * @param {HTMLElement} elm
 *
 * @returns {number}
 */
const _getHeight = elm => {
	 if (!elm)
		return 0;

	const {
		marginTop,
		marginBottom,
		height
	} = window.getComputedStyle(elm);

	const total = safeParseInt(height)
		+ safeParseInt(marginTop)
		+ safeParseInt(marginBottom);

	return total;
};


/**
 * Truncate text content to fit element height, adding an ellipsis
 * to the end
 *
 * @param {HTMLElement} elm
 */
const _truncateOverflowText = elm => {
	const height = elm.clientHeight;

	elm.style.visibility = "visible";

	while (elm.scrollHeight > height) {
		elm.innerText = elm.innerText.replace(/\W*\s(\S)*$/, '...');
	}

	elm.style.visibility = "";
};

/**
 * Get the maximum height that the body text element can take up
 *
 * @param {HTMLElement} wrapper
 * @param {HTMLElement} heading
 * @param {HTMLElement} elm
 * @param {HTMLElement} button
 *
 * @returns {number}
 */
const _getMaxHeight = (wrapper, heading, elm, button) => {
	const wrapperHeight = _getHeight(wrapper);
	const headingHeight = _getHeight(heading);
	const buttonHeight = _getHeight(button) || NOW_BUTTON_HEIGHT;

	const { marginTop, marginBottom } = window.getComputedStyle(elm);
	const margins = safeParseInt(marginTop) + safeParseInt(marginBottom);

	return wrapperHeight - (buttonHeight + headingHeight + margins);
};


const view = state => {
	const {
		backgroundImage,
		backgroundTransparent,
		heading,
		bodyText,
		buttonText,
		buttonLink,
		handleButtonClick,
		visible,
		slideSpeed,
		transition,
		richText,
		useCustomHtml,
		carouselId,
		index,

	} = state.properties;

	const classes = {
		'slide-wrapper': true,
		'hidden': !visible,
		'overlay': backgroundImage,
		'-empty': !backgroundImage && !useCustomHtml,
		'rich-text': useCustomHtml
	};

	if (transition) {
		const className = kebabCase(transition).toLowerCase();
		classes[className] = true;
	}

	const hasButton = buttonText && buttonLink;

	const buttonRef = createRef();
	const headingRef = createRef();
	const wrapperRef = createRef();

	return (
        <div
			hook-insert={vnode => {
				const style = vnode.elm && vnode.elm.style;

				if (style) {
					style.animationDuration = `${slideSpeed}ms`;
					style.backgroundImage = backgroundImage && `url(/${backgroundImage})`;
				}
			}}
			class={classes}
			style={backgroundTransparent ? { backgroundColor: "transparent" } : {}}
			aria-hidden={visible ? "false" : "true"}
			role="tabpanel"
			aria-labelledby={`tab_${carouselId}_${index}`}
			id={`tabpanel_${carouselId}_${index}`}
		>
			<div className="slide" role={visible ? "alert" : ""}>
				{useCustomHtml ?
					<now-rich-text html={richText}></now-rich-text>
					: (
						<div className="slide-content-wrapper">
							<div className="slide-content">
								<section ref={wrapperRef}>
									<div>
										<h3
											class={{
												'-empty': !backgroundImage
											}}
											ref={headingRef}
										>
											{heading}
										</h3>
										<p
											class={{
												'-empty': !backgroundImage
											}}
											hook-insert={vnode => {
												const { elm } = vnode;
												const maxHeight = _getMaxHeight(
													wrapperRef.current,
													headingRef.current,
													elm,
													buttonRef.current
												);

												if (elm.scrollHeight > maxHeight) {
													elm.style.maxHeight = `${maxHeight}px`;
													_truncateOverflowText(elm);
												}
											}}
										>
											{bodyText}
										</p>
										{hasButton ? (
											<now-button
												ref={buttonRef}
												label={buttonText}
												variant="primary"
												on-click={() => handleButtonClick(buttonLink)}
											></now-button>
										) : ""}
									</div>
							</section>
						</div>
					</div>
					)
				}
			</div>
		</div>
    );
};

export default view;
