import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import {
	AUTOSCROLL_START,
	AUTOSCROLL_STOP,
	NEXT_SLIDE,
	PREVIOUS_SLIDE,
	SET_SLIDE,

	directions,
	transitions
} from './constants';
import {t} from 'sn-translate';

/**
 * Generates a random alphanumeric string of a specified length
 * */
const carouselId = ((length) =>{
	let result           = '';
	const characters       = 'abcdefghijklmnopqrstuvwxyz0123456789';
	const charactersLength = characters.length;
	for ( let i = 0; i < length; i++ )
	   result += characters.charAt(Math.floor(Math.random() * charactersLength));
	return result;
 })(20);

/**
 * Get the correct transition for a slide based on the context
 *
 * @param {number} slideDirection
 * @param {boolean} isActive
 *
 * @returns {('SLIDE_IN_FROM_LEFT'|'SLIDE_IN_FROM_RIGHT'|'SLIDE_OUT_TO_LEFT'|'SLIDE_OUT_TO_RIGHT'\null)}
 */
const _getTransition = (slideDirection, isActive) => {
	switch(slideDirection) {
		case (directions.LEFT):
			return isActive ? transitions.SLIDE_IN_FROM_LEFT : transitions.SLIDE_OUT_TO_RIGHT;
		case (directions.RIGHT):
			return isActive ? transitions.SLIDE_IN_FROM_RIGHT : transitions.SLIDE_OUT_TO_LEFT;
		default:
			return null;
	}
};


/**
 * Determine whether the currently active slide has a backgroundImage property
 *
 * @param {object} state
 *
 * @returns {boolean}
 */
const _isBackgroundEmpty = state => {
	const { properties } = state;
	const { active, slides } = properties;

	if (!slides[active])
		return false;

	return !slides[active].backgroundImage;
};


/**
 * Create a single button for the 'dots' navigation
 *
 * @param {number} slide The index of the slide that this button navigates to
 * @param {boolean} active Whether this button navigates to the current slide
 * @param {function} dispatch
 *
 * @returns {JSX}
 */
const _createPaginationButton = (slide, active, dispatch) => {
	const variant = active ? 'primary' : 'secondary';

	return (
			<button
				aria-label={t('Slide {0}', slide + 1)}
				aria-controls={`tabpanel_${carouselId}_${slide}`}
				aria-selected={active ? 'true': 'false'}
				id={`tab_${carouselId}_${slide}`}
				role="tab"
				tabindex="0"
				className={variant}
				on-click={() => {
					if (!active)
						dispatch(SET_SLIDE, { slide });
				}}
			></button>

    );
};


/**
 * Create the 'dots' pagination
 *
 * @param {object} state
 * @param {function} dispatch
 *
 * @returns {JSX}
 */
const _createPagination = (state, dispatch) => {
	const { slides, active } = state.properties;

	if (!slides || slides.length <= 1) return;

	const paginationBtns = slides.map((s, i) => {
		const isActive = active === i;
		return _createPaginationButton(i, isActive, dispatch);

	});

	return (
		<div className="pagination" role="tablist">
			{paginationBtns}
		</div>
	);
};


/**
 * Create the navigation arrows
 *
 * @param {object} state
 * @param {function} dispatch
 *
 * @returns {JSX}
 */
const _createNavigation = (state, dispatch) => {
	const { slides, navButtonLabels } = state.properties;
	if (!slides || slides.length <= 1) return;

	const backgroundIsEmpty = _isBackgroundEmpty(state);

	return (
        <Fragment>
			<div className="left-navigation-button">
				<now-button-bare
					size="lg"
					config-aria={{
						'aria-label': t(navButtonLabels.previous)
					}}
					variant={backgroundIsEmpty ? 'secondary' : 'primary'}
					icon-start="chevron-left-fill"
					on-click={() => {
						dispatch(PREVIOUS_SLIDE)
					}}
				></now-button-bare>
			</div>
			<div className="right-navigation-button">
				<now-button-bare
					size="lg"
					config-aria={{
						'aria-label': t(navButtonLabels.next)
					}}
					variant={backgroundIsEmpty ? 'secondary' : 'primary'}
					icon-start="chevron-right-fill"
					on-click={() => {
						dispatch(NEXT_SLIDE)
					}}
				></now-button-bare>
			</div>
		</Fragment>
    );
};

/**
 * Creates the slides to render inside of the component
 *
 * @param {object} properties
 *
 * @returns {JSX}
 */
const _createSlides = properties => {
	const {
		slides,
		slideSpeed,
		active,
		isSliding,
		slideDirection,
		handleButtonClick,
		lastActive
	} = properties;

	if (!slides.length)
		return "";

	const slideElms = slides.map((slide, i) => {
		const isActive = i === active;
		const isLastActive = i === lastActive;

		const transition = isSliding
			&& (isActive || isLastActive)
			&& _getTransition(slideDirection, isActive);

		return (
			<sn-component-carousel-slide
				heading={slide.heading}
				background-image={slide.backgroundImage}
				background-transparent={slide.backgroundTransparent}
				button-text={slide.buttonText}
				button-link={slide.buttonLink}
				body-text={slide.bodyText}
				handle-button-click={handleButtonClick}
				slide-speed={slideSpeed}
				visible={isActive}
				transition={transition}
				index={i}
				carouselId={carouselId}
				use-custom-html={slide.useCustomHtml}
				rich-text={slide.richText}
			></sn-component-carousel-slide>
		);
	});

	return slideElms;
};


const view = (state, { dispatch }) => {
	const { properties } = state;
	const {
		height,
		autoscroll,
		roundedCorners
	} = state.properties;

	return (
		<section
			class={{"main": true, "rounded-corners": roundedCorners}}
			hook-insert={vnode => {
				vnode.elm.style.height = `${height}px`;
			}}
			on-mouseenter={() => {
				if (autoscroll)
					dispatch(AUTOSCROLL_STOP);
			}}
			on-mouseleave={() => {
				if (autoscroll)
					dispatch(AUTOSCROLL_START);
			}}
			on-focusin={() => {
				if (autoscroll)
					dispatch(AUTOSCROLL_START);
			}}
			on-focusout={() => {
				if (autoscroll)
					dispatch(AUTOSCROLL_STOP);
			}}
		>
			<slot name="slides">
				<div className="slides">
					{_createSlides(properties)}
				</div>
			</slot>
			<slot name="navigation">
				{_createNavigation(state, dispatch)}
			</slot>
			<slot name="pagination">
				{_createPagination(state, dispatch)}
			</slot>
		</section>
	);
};

export default view;
